<script>
// @ is an alias to /src
// import LineChart from '@/components/charts/LineChart.vue'
// import BarChart from '@/components/charts/BarChart.vue'
import PieChart from '@/components/charts/PieChart.vue'
import Doughnut from '@/components/charts/Doughnut.vue'
import { mapGetters } from 'vuex'

export default {
  name: 'MonthlyReport',
  components: {
    PieChart,
    Doughnut
  },
  computed: {
    ...mapGetters([
      "getEmployeeDataSet",
      "getVisitorDataSet",
      "getTPCDataSet",
      "analyticsAllGender",
      "analyticsAllEmployeeAgeGroup",
    ]),
  },
  data() {
    return {
      month: new Date().toISOString().substr(0, 7),
      menu: false,
      loading: null,
    }
  },
  mounted() {
    if (!localStorage.isLoggedIn) {
      this.$router.push("/");
    }
    
  },
  methods: {
    async changeDate(){
      this.$refs.menu.save(this.month)
      this.getETriageAnalytics();
    },
    async getETriageAnalytics() {
      this.loading = true
      await this.$store.dispatch("getETriageAnalytics",this.month);
      this.loading = false
    },
  }
}
</script>
