<script>
import { Doughnut } from 'vue-chartjs'

export default {
  extends: Doughnut,
  props: [
    "loading", 
    "dataSets",
    "dataHeads",
    "label",
    "backgroundColor",
  ],

  data() {
    return {
      chartdata: {
        datasets: [{
          backgroundColor: this.backgroundColor,
          data: this.dataSets,
        }],
        labels: this.label,
      },
      options: {
        plugins: {
					datalabels: {
            borderRadius: 4,
            borderColor: 'black',
            backgroundColor: 'white',
						color: 'black',
						font: {
							weight: 'bold'
						},
					}
        },
        layout: {
          padding: {
              left: 10,
              right: 10,
              top: 0,
              bottom: 0
          }
        },
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },
  mounted () {
    if(!this.loading && this.loading != null) {
      this.renderChart(this.chartdata, this.options)
    }
  }
}
</script>
